import { createGlobalStyle } from "styled-components";

export const GlobalStyles = createGlobalStyle`
     @font-face {
        font-family: 'Saira Condensed';
        font-style: normal;
        font-weight: 400;
        font-display: swap;
        src: url(./fonts/EJROQgErUN8XuHNEtX81i9TmEkrvoutF.woff2) format('woff2');
        unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
    }
    @font-face {
        font-family: 'Saira Condensed';
        font-style: normal;
        font-weight: 500;
        font-display: swap;
        src: url(./fonts/EJRLQgErUN8XuHNEtX81i9TmEkrnUchQ962f.woff2) format('woff2');
        unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
    }
    @font-face {
        font-family: 'Saira Condensed';
        font-style: normal;
        font-weight: 600;
        font-display: swap;
        src: url(./fonts/EJRLQgErUN8XuHNEtX81i9TmEkrnfc9Q962f.woff2) format('woff2');
        unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
    }
    @font-face {
        font-family: 'Saira Condensed';
        font-style: normal;
        font-weight: 700;
        font-display: swap;
        src: url(./fonts/EJRLQgErUN8XuHNEtX81i9TmEkrnGc5Q962f.woff2) format('woff2');
        unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
    }
    * {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }
    html {
        font-size: 62.5%;
        font-family: 'Saira Condensed', sans-serif;
        user-select: none;
        @media screen and (max-width: 768px) {
        font-size: 45%;
     }
        @media screen and (max-width: 617px) {
        font-size: 40%;
     }
        @media screen and (max-width: 360px) {
        font-size: 30%;
     }
    }
    body {
        background: linear-gradient(180deg, rgba(24,20,26,1) 10%, rgba(35,31,40,1) 100%);
    }
    div {
        overflow: hidden;
    }
    button {
        font-family: 'Saira Condensed', sans-serif;
        padding: 0.8rem 2.5rem;
        font-size: 2rem;
        font-weight: 500;
        background: transparent;
        color: white;
        cursor: pointer;
        border: 1.5px solid white;
        &:hover {
            background: white;
            color: black;
        }
        @media screen and (max-width: 768px) {
        padding: 1.2rem 3rem;
        }
    }
    h2 {
        font-size: 10rem;
        width: fit-content;
    }
    h3{
        font-size: 4rem;
    }
    h4{
        font-size: 4rem;
    }
`;
